*{
}
.listcontainer {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    width: 100%;
    gap: 20px; /* 요소들 사이의 간격 추가 */
}
.list-group {
    list-style: none;
    display: flex;
    flex-direction: column;
    justify-content: center;
    gap: 20px;
    width: 100%;
}
.listcontainer2 {
    background: rgba(113, 113, 113, 0.6);
    gap: 20px;
    margin-bottom: 20px;
    width: 100%;
    height: 90px;
    border-radius: 10px;
    padding: 10px;/* 요소들 사이의 아래쪽 간격 추가 */
}
.listcon {
    justify-content: center;
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 80%;
    gap: 20px; /* 요소들 사이의 간격 추가 */
}
.listcontainer2>div:nth-child(1) {
    font-size: 10px;
    color: gray;
    letter-spacing: 1px; /* 글자들 사이의 간격 추가 */
    line-height: 1.5; /* 줄 사이의 간격 추가 */
}
.listcontainer2>div>span:nth-child(1) {
    font-size: 30px;
    font-weight: bold;
    letter-spacing: 1px; /* 글자들 사이의 간격 추가 */
    line-height: 1.5; /* 줄 사이의 간격 추가 */
}
.listcontainer2>div>span:nth-child(2) {
    font-size: 10px;
    color: gray;
    letter-spacing: 1px; /* 글자들 사이의 간격 추가 */
    line-height: 1.5; /* 줄 사이의 간격 추가 */
}
.listcontainer2>div:nth-child(2) {
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex-direction: row;
}
.modal-container {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: rgba(0,0,0,0.5); /* 반투명 배경 */
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 1000; /* 다른 요소들 위에 표시되도록 높은 값 설정 */

}
.modalcontent {
    padding: 20px;
    border-radius: 5px;
    width: 700px;
    gap: 20px;
    height: 250px;
    background-color: rgba(0,0,0,0.7);
    justify-content: center;
    display: flex;
    flex-direction: column;
    border : 1px solid gray;
    row-gap: 20px; /* 요소들 사이의 수직 간격 추가 */
}
.modalclose {
    cursor: pointer;
    border: none;
    background: none;
    font-size: 16px;
    color:white;
    margin-top: 10px;
}
.modal-con {
    width: 500px;
}
.modalpicture {
    width: 100%;
    height: 80%;
    display: flex;
    align-items: center;
}
.modalpicture>span>span:nth-child(1) {
    font-weight: bold;
    font-size: 30px;
    letter-spacing: 1px; /* 글자들 사이의 간격 추가 */
    line-height: 1.5; /* 줄 사이의 간격 추가 */
}
.search {
    width: 200px;
    height: 25px;
    font-size: 15px;
    border: 0;
    border-radius: 15px;
    outline: none;
    padding-left: 10px;
    margin-top: 20px;
    margin-bottom: 20px;
    letter-spacing: 1px; /* 글자들 사이의 간격 추가 */
    line-height: 1.5; /* 줄 사이의 간격 추가 */
}
.searchbutton {
    width: 30px;
    height: 25px;
    outline: none;
    border: none;
    border-bottom: 1px solid blue;
    gap: 20px;
}
.spot {
    background-image: url("sea-84629.jpg");
    width: 100%;
    height: 350vh;
    background-position: center;
    background-size: cover;
    background-repeat: no-repeat;
}
.spots-search-container{
    width: 250px;
    display: flex;
    justify-content: space-between;
    align-items: center;
}
.modal-content-title{
    font-size: 40px;
    color: white;
    font-weight: bold;
    margin-bottom: 10px;
}
.modal-content-doc{
    margin-bottom: 5px;
    color: white;
}
.modalimg{
    border-radius: 10px;
    margin-right: 10px;
    margin-bottom: 100px;
}