.community-container {
    width: 800px;
    height: 100px;
    border: 1px solid black;
    margin-bottom: 10px;
    border-radius: 10px;
    margin-top: 10px;
    background-color: rgba(0,0,0,0.8);
    display: flex;
    padding-left: 10px;
}
.community-container > div {
    display: flex;
    align-items: center;
    gap: 20px;
}
.community-container > div > h3 {
    margin-bottom: 10px;
    font-size: 30px;
    color: #6398FF;
}
.communitycon {
    justify-content: center;
    display: flex;
    align-items: center;
}
.modal-container2 {
    width: 400px;
    height: 500px;
    border: 1px solid black;
    display: flex;
    align-items: center;
    flex-direction: column;
    background-color: rgba(0,0,0,0.8);
}
.modal-container2-title {
    width: 90%;
    margin-top: 10px;
    border: 0.1px solid black;
    margin-bottom: 10px;
    background-color: #4c4c4c;
}
.modal-container2-title::placeholder {
    color: white;
}
.modal-container2-content {
    width: 90%;
    height: 200px;
    border: 0.1px solid black;
    background-color: #4c4c4c;
}
.modal-container2-content::placeholder {
    color: white;
}
.modal-container2-close {
    color: white;
    margin-top: 70px;
}
.modal-container2-image {
    width: 250px;
    border: 1px solid black;
    margin-top: 10px;
    background-color: #4c4c4c;
    color: white;
}
.modal-container2-image::placeholder {
    color: white;
    background-color: #4c4c4c;
}
.modal-container2-upload {
    width: 350px;
    height: 50px;
    margin-top: 10px;
    margin-bottom: 10px;
    border: none;
    background-color: #6398FF;
    color: white;
    border-radius: 5px;
}
.community-box {
    background-image: url("sea-84629.jpg");
    width: 100%;
    min-height: 100vh; /* 최소 높이를 100vh로 설정 */
    background-position: center;
    background-size: cover;
    background-repeat: no-repeat;
    overflow-y: auto; /* 세로 스크롤 가능하게 설정 */
}
.modal-content {
    width: 500px;
    height: 60%;
    display: flex;
    flex-direction: column;
    align-items: center;
    background-color: rgba(0,0,0,0.8);
    border-radius: 10px;
    padding: 10px;
}
.modal-content > div {
    display: flex;
    flex-direction: column;
    align-items: center;
}
.modal-content > div > h1 {
    color: white;
}
.modal-content > div > div {
    align-items: center;
    display: flex;
    max-width: 500px;
    flex-wrap: wrap;
    justify-content: center;
    height: 300px;
}
.modal-content > div > p {
    color: white;
}
.p {
    font-size: 20px !important;
}
.modal-close {
    position: absolute;
    top: 75%;
    width: 20px;
    height: 20px;
    background-color: rgba(0, 0, 0, 0);
    border: none;
    color: white;
}
.modal-close:hover {
    cursor: pointer;
}
.community-container-head {
    color: white;
    margin: 10px;
    font-weight: bold;
    font-size: 50px;
}
.addbutton {
    width: 80px;
    height: 80px;
    border-radius: 50%;
    background-color: #6398FF;
    justify-content: center;
    align-items: center;
    display: flex;
    position: absolute;
    top: 85%;
    left: 92%;
    font-size: 40px;
    color: white;
}
.addbutton:hover {
    cursor: pointer;
}

.custom-file-upload-button {
    width: 350px;
    height: 50px;
    margin-top: 10px;
    margin-bottom: 10px;
    border: none;
    background-color: #6398FF;
    color: white;
    display: flex;
    align-items: center;
    justify-content: center;
    cursor: pointer;
    border-radius: 5px;
    font-size: 16px;
}

.custom-file-upload-button:hover {
    background-color: #537DD5;
}
.modal-container2-upload:hover {
    background-color: #537DD5;
}