.modal{
    width: 76%;
    height: 92.7%;
    position: absolute;
    top: 1.7%;
    left: 37vh;
    padding: 20px;
    border: 1px solid #000;
    background-color: rgba(0,0,0,0.6);
    border-radius: 15px;
}
.howtorecycle-container{
    background:rgba(0,0,0,0.8)url("sea-84629.jpg");
    width: 100%;
    height: 100vh;
    background-position: center;
    background-size: cover;
    background-repeat: no-repeat;
}
.groupedPrint{
    background-color: rgba(0,0,0,0.8);
    width: 15%;
    display: flex;
    flex-direction: column;
    padding-left: 1%;
    padding-top: 1%;
    height: 95%;
    border-radius: 15px;

}
.groupedPrint-head{
    font-size: 30px;
    font-weight: bold;
    color: white;
}
.groupedPrint-container{
    display: flex;
    align-items: center;
    flex-direction: row ;

    padding-left: 1%;
    width: 100%;
    height: 100%;
}
.hamimg{
    width: 30px;
    height: 30px;
}
.groupedPrint-title{
    margin-top: 5%;
    margin-bottom: 5%;
    font-size: 30px;
    font-weight: bold;
    color: white;
}
.listp{
    color: white;
}
.groupedPrint-list{
    text-decoration: none;
    list-style: none;
}
.groupedPrint-head.clicked {
    color: #6398FF; /* 클릭된 초성의 색상 */
    font-weight: bold; /* 예시로 글꼴을 두껍게 설정 */
}
.modal-head{
    font-size: 55px;
    font-weight: bolder;
    color: #6398FF;
    margin-top: 1%;
    margin-bottom: 1%;
}
.modal-content>p{
    font-size: 35px;
    margin-top: 10px;
    color: white;
}
.close{
    color: white;
   font-size: 20px;
}
.hamburger{
    color: white;
    text-decoration: none;
}