* {
    font-family: "Wanted Sans";
    margin: 0;
    padding: 0;
    text-decoration: none;
}
.mainpage-container{
    background-image: url("sea-84629.jpg");
    background-size: cover;
    width: 100%;
    height: 100vh;
    color: white;
}
.mainpage-list{
    margin-left: 20px;
}
.liststyle{
    margin-top: 10px;
    list-style: none;
    line-height: 300%;

}
.mainpage-list>ul>li:hover{
    font-weight: bold;
}
.mainpage-list>ul>li{
    color: white;
}
.mainpage-list>ul>li a {
    color: inherit;
}
.mainpage-list>ul>li p {
    color: inherit;
}
